<template>
    <div id="createProjectPage" class="container">
    
        <div class="row">
    
            <!-- Steps -->
            <div class="col-md-2">
                <el-steps :active="activeStep" finish-status="success" align-center direction="vertical" style="height: 450px; margin-top: 50px;">
                    <el-step title="Create Account" description=""></el-step>
                    <el-step title="Create Project" description=""></el-step>
                    <el-step title="Setup Front-end" description=""></el-step>
                    <el-step title="Setup Back-end" description=""></el-step>
                    <el-step title="Test Setup" description=""></el-step>
                </el-steps>
            </div>
            <!-- Tabs -->
            <div class="col-md-10">
                <div style="float: right; margin: 7px">
                    <el-button>Logout</el-button>
    
                </div>
    
                <!-- Create Project Tab -->
                <div id="createProjectTab" class="tabDiv container" v-if="currentDivToShow == 'createProject'">
                    <div class="tabHeader">
                        <h3>Let's create your first project</h3>
                        <div class="description">
                            A Growlytics project is where you view and manage information detected in your website and server.
                            <br /> To start you just need to give it a name, typically the same as your app or source code repository.
                        </div>
                    </div>
                    <el-form ref="form" :model="form" label-width="120px">
                        <el-form-item label="Project Name">
                            <el-input v-model="projectName" required></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="onCreateProject()">Create Project</el-button>
                        </el-form-item>
                    </el-form>
                </div>
    
                <!-- Setup Frontend Tab -->
                <div id="setupFrontendTab" class="tabDiv container" v-if="currentDivToShow == 'setupFrontend'">
                    <div class="tabHeader">
                        <h3>Select Growlytics Browser SDK</h3>
                        <div class="description">
                            Browser SDKs integrate with your app to send errors and messages to Rollbar.
                            <br /> To start you just need to give it a name, typically the same as your app or source code repository.
                        </div>
    
                        <!-- Framework List -->
                        <div class="row frameworkListContainer">
                            <div class="col-md-3" v-for="(framework, frameworkId) in frontendFrameworks">
                                <div class="frameworkDiv" v-bind:class="{selected: frameworkId == selectedFrontEndFramework}" @click="selectedFrontEndFramework = frameworkId">
                                    <i style="font-size: 60px;" v-bind:class="framework.class"></i>
                                    <div class="frameworkName">
                                        {{framework.name}}
                                    </div>
                                </div>
                            </div>
                        </div>
    
                        <div class="installationStepsContainer">
    
                            <div class="installationHeader">Installation Steps For {{selectedFrontEndFramework}}</div>
    
                            <!-- Setup Steps for Javascript -->
                            <div>
                                To use the JavaScript SDK, simply copy-paste the following code into the
                                <code>&lt;head&gt;</code> of any page you'd like to monitor. Add it before all other
                                <code>&lt;script&gt;</code> tags to ensure that errors in those scripts are reported to Growlytics.
                            </div>
    
                            <!-- Setup script -->
                            <div class="installationStep">
                                <pre style="solarized-dark" v-highlightjs="frontendFrameworks[selectedFrontEndFramework].scriptData"><code class="javascript" style="solarized-dark"></code></pre>
    
                            </div>
                        </div>
    
                    </div>
                    <el-button type="primary" @click="currentDivToShow = 'setupBackend'">I have put front-end code in my project </el-button>
                </div>
    
                <!-- Setup Backend Tab -->
                <div id="setupBackendTab" class="tabDiv container" v-if="currentDivToShow == 'setupBackend'">
    
                    <div class="tabHeader">
                        <h3>Select Growlytics Server side SDK</h3>
                        <div class="description">
                            Browser SDKs integrate with your app to send errors and messages to Growlytics.
                            <br /> To start you just need to give it a name, typically the same as your app or source code repository.
                        </div>
                    </div>
    
                    <!-- Framework List -->
                    <div class="row frameworkListContainer">
                        <div class="col-md-3" v-for="(framework, frameworkId) in backendFrameworks">
                            <div class="frameworkDiv" v-bind:class="{selected: frameworkId == selectedBackEndFramework}" @click="selectedBackEndFramework = frameworkId">
                                <i style="font-size: 60px;" v-bind:class="framework.class"></i>
                                <div class="frameworkName">
                                    {{framework.name}}
                                </div>
                            </div>
                        </div>
                    </div>
    
                    <!-- Framework specific instructions -->
                    <div class="installationStepsContainer">
    
                        <div class="installationHeader">Installation Steps For {{backendFrameworks[selectedBackEndFramework].name}}</div>
    
                        <!-- Setup Instructions for Node-express -->
                        <div v-if="selectedBackEndFramework == 'nodeexpress'">
                            To use the JavaScript SDK, simply copy-paste the following code into the
                            <code>&lt;head&gt;</code> of any page you'd like to monitor. Add it before all other
                            <code>&lt;script&gt;</code> tags to ensure that errors in those scripts are reported to Growlytics. Add
    
                            <!-- Add npm -->
                            <div class="installationStep">
                                <div class="stepTitle">Step 1: Add Growlytics package to your express application using npm</div>
                                <pre style="solarized-dark" v-highlightjs><code class="javascript" style="solarized-dark">npm install growlytics-node</code></pre>
                            </div>
    
                            <!-- Require growlytics in your app -->
                            <div class="installationStep">
                                <div class="stepTitle">Step 2: Require growlytics in your app & register your api key</div>
                                <pre style="solarized-dark" v-highlightjs="backendFrameworks[selectedBackEndFramework].scriptData"><code class="javascript" style="solarized-dark"></code></pre>
                            </div>
    
    
                            <!-- Add Request handler middleware -->
                            <div class="installationStep">
                                <div class="stepTitle">Step 3: Install growlytics request handler middleware</div>
                                <pre style="solarized-dark" v-highlightjs><code class="javascript" style="solarized-dark">app.use(growlytics.requestHandler());</code></pre> To ensure that apis are captured by growlytics, add the requestHandler middleware to your app as the
                                <b>first middleware.</b>
                            </div>
    
                            <!-- Add Error handler middleware -->
                            <div class="installationStep">
                                <div class="stepTitle">Step 4: Install growlytics error handler middleware</div>
                                <pre style="solarized-dark" v-highlightjs><code class="javascript" style="solarized-dark">app.use(growlytics.errorHandler());</code></pre> To ensure that unhandled errors are captured by growlytics, add the errorHandler middleware to your app as the
                                <b>last middleware.</b>
                            </div>
    
                            <!-- Writing logs -->
                            <div class="installationStep">
                                <div class="stepTitle">Step 5: Write debugging logs to growlytics logger</div>
                                <pre style="solarized-dark" v-highlightjs><code class="javascript" style="solarized-dark">app.get("/", function (req, res, next) {
                                        req.getGrowlyticsLogger().info("message...", { dta: "data" }, null); 
                                        req.getGrowlyticsLogger().debug("message...", { dta:"data" }, null); 
                                        req.getGrowlyticsLogger().warning("message...", { dta: "data" }, null); 
                                        req.getGrowlyticsLogger().error("message...", { dta: "data" }, null);
                                        req.send('response...');
                                    });
        </code></pre> Writing logs to growlytics is a bit tricky. The logger instance is only available from the express's request object.
                            </div>
    
                        </div>
    
                        <!-- Setup Instructions for Node-express -->
                        <div v-if="selectedBackEndFramework == 'laravel5'">
                            <div style="margin-top: 100px; margin-bottom: 100px; text-align: center; font-size: 20px; color: #ddd;">
                                Growlytics support for Laravel is not available yet
                            </div>
                        </div>
    
                    </div>
                    <br />
                    <br />
                    <el-button type="primary" @click="currentDivToShow = 'setupComplete'">I have put back-end code in my project </el-button>
                </div>
    
                <!-- Setup Backend Tab -->
                <div id="setupBackendTab" class="tabDiv container" v-if="currentDivToShow == 'setupComplete'" style="text-align: center;">
                    <i style="font-size: 200px; margin-top: 50px; color: #008000b3;" class="far fa-check-circle"></i>
                    <div style="margin: 30px; color: #777;">Setup complete</div>
                    <el-button style="margin-bottom: 50px;" type="primary" @click="goToDashboard()">Take me to dashboard</el-button>
                </div>
    
            </div>
    
        </div>
    </div>
</template>

<script>
    import CreateProjectComponent from "./createProjectComponent";
    export default CreateProjectComponent;
</script>

<style lang="scss">
    #createProjectPage {
        min-height: 100vh;
        margin-top: 30px;
        .tabDiv {
            background: white;
            border-radius: 5px;
            padding: 30px;
            padding-left: 20%;
            padding-right: 20%;
            .tabHeader {
                padding-top: 20px;
                padding-bottom: 30px;
                width: 100%;
                margin: auto;
                text-align: center;
                .description {
                    color: #777;
                    font-size: 12px;
                }
            }
            .frameworkListContainer {
                margin-top: 30px;
                margin-bottom: 30px;
                .frameworkDiv {
                    border: 1px solid #ddd;
                    padding: 10px;
                    border-radius: 5px;
                    .frameworkName {
                        font-size: 14px;
                        color: #666;
                        margin-top: 10px;
                    }
                }
                .selected {
                    background: #9e9e9e30;
                    border: 1px solid #9e9e9e3d;
                }
            }
            .installationStepsContainer {
                /* border: 1px solid red; */
                text-align: justify !important;
                margin-top: 60px;
                font-size: 13px;
                color: #555;
                line-height: 20px;
                .installationHeader {
                    color: #333;
                    font-size: 25px;
                    margin-bottom: 10px;
                }
                .installationStep {
                    margin-top: 20px;
                    .stepTitle {
                        font-size: 15px;
                        font-weight: bold;
                        margin-bottom: 5px;
                    }
                }
            }
        }
        #createProjectTab {}
    }
</style>


