import { getScriptForFramework, createProject } from "../../../services/project";


export default {
    name: "register",
    data() {
        return {
            companyCode: '',
            projectName: '',
            errorMsg: '',
            currentDivToShow: 'createProject',
            // currentDivToShow: 'setupFrontend',
            // currentDivToShow: 'setupBackend',
            // currentDivToShow: 'setupComplete',

            form: {},
            selectedFrontEndFramework: 'javascript',
            selectedBackEndFramework: 'nodeexpress',
            frontendFrameworks: {
                javascript: { name: 'Javascript', class: "devicon-javascript-plain colored", scriptFile: 'javascript.js', scriptData: '' },
                react: { name: 'React', class: "devicon-react-original colored", scriptFile: 'react.js', scriptData: '' },
                angular2: { name: 'Angular 2', class: "devicon-angularjs-plain colored", scriptFile: 'angular2.js', scriptData: '' },
                vuejs: { name: 'Vue Js', class: "devicon-vuejs-plain colored", scriptFile: 'vuejs.js', scriptData: '' }
            },
            backendFrameworks: {
                nodeexpress: { name: 'Express Js', class: "devicon-express-original-wordmark colored", scriptFile: 'node-express.js', scriptData: '' },
                laravel5: { name: 'Laravel 5.x', class: "devicon-laravel-plain colored", scriptFile: 'laravel.js', scriptData: '' }
            }
        };
    },
    methods: {
        onCreateProject() {
            
            let params = {
				company_code: this.$store.state.userInfo.companyCode,
				name: this.projectName
			};
             
			createProject(params)
				.then(result => {
					if (result.data.success) {
                        this.$store.dispatch("setProjectInfo",{
                            "projectId": result.data.data.projectId,
                            "projectStatus": result.data.data.project,
                            "apiKey": result.data.data.apiKey
                            // "userId": result.data.userInfo.userId
                        });
                        this.currentDivToShow = 'setupFrontend';
					} else {
						//this.$toast.error(result.data.message);
						this.error("Failed to save project.");
					}
				})
				.catch(error => {
                    this.error("Internal Error");
				});
        },
        goToDashboard() {
            this.$router.replace({ name: 'SessionList' });
        }
    },
    computed: {
        activeStep: function () {
            if (this.currentDivToShow == 'createProject')
                return 1;
            else if (this.currentDivToShow == 'setupFrontend')
                return 2;
            else if (this.currentDivToShow == 'setupBackend')
                return 3;
            else
                return 4;
        }
    },
    created() {
        this.$store.dispatch("setHeaderVisiblity", false);

        //Fetch data for each
        _.forEach(this.frontendFrameworks, function (framework) {
            getScriptForFramework(framework.scriptFile).then((response) => {
                framework.scriptData = response.data;
            }).catch((error) => {
                console.error(framework.scriptFile + ' data read error ==>', error);
            });
        })

        //Fetch data for each
        _.forEach(this.backendFrameworks, function (framework) {
            console.log(framework.scriptFile);
            getScriptForFramework(framework.scriptFile).then((response) => {
                framework.scriptData = response.data;
            }).catch((error) => {
                console.error(framework.scriptFile + ' data read error ==>', error);
            });
        })



    }
};


